@import "./stars.scss";
@import "./utils/variables";
@import "./utils/extends";

.products {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

  width: 992px;
  padding: 50px 0 25px;
  margin: 0 auto;

  .card {
    @extend %column-direction;

    &:not(:nth-last-child(-n + 4)) {
      margin-bottom: 46px;
    }

    max-width: 200px;
    padding: 32px 16px 16px;
    margin-left: $card-row-gap;
    margin-right: $card-row-gap;

    border: 1px solid #f3f3f3;

    &__img {
      width: 160px;
      height: 134px;
      margin: 0 auto 40px;
    }

    &__title {
      margin-bottom: 4px;

      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
      color: $color-black;
    }

    &__product-code {
      margin-bottom: 16px;

      font-size: 10px;
      font-weight: 400;
      color: $color-gray;
      line-height: 14px;
    }

    &__rating {
      @extend %row-direction;

      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 24px;

      line-height: 14px;
    }

    &__reviews {
      font-size: 10px;
      font-weight: 400;
      color: $color-black;
    }

    &__price {
      @extend %row-direction;

      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 16px;

      line-height: 18px;

      &-title {
        font-size: 12px;
        font-weight: 400;
        color: $color-gray;
      }

      &-uah {
        font-size: 16px;
        font-weight: 700;
        color: $color-black;
      }
    }

    &__button {
      padding: 10px 55px 11px;

      font-size: 14px;
      font-weight: 700;
      text-transform: uppercase;
      text-decoration: none;
      color: #fff;

      border: 1px solid $color-blue;
      background-color: $color-blue;
      border-radius: 5px;

      &:hover {
        color: $color-blue;
        background-color: #fff;
      }
    }
  }
}
